.thank-you-card {
  border: 0 none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0);
  background-color: rgba(255, 255, 255, 0);
  color: #4c5050;
}

.thank-you-card p {
  margin: 0;
  text-align: start;
}

.thank-you-card a {
  text-decoration: none;
}

.thank-you-card .list-div {
  margin-top: 20px;
}

.thank-you-card .star-div {
  text-align: start;
  margin-top: 10px;
}

.thank-you-card img {
  width: 100%;
  margin: 20px 10px;
  border-radius: 20px;
}

.thank-you-card .btn-a {
  font-weight: 700;
}
