.loading-dots-step {
  display: flex;
  margin: 30px auto 30px auto;
  justify-content: center;
}
.loading-dots-step .dot {
  position: relative;
  width: 12px;
  height: 12px;
  margin: 12px;
  border-radius: 50%;
}
.loading-dots-step .dot::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  animation: wave 2s ease-out infinite;
}
.loading-dots-step .dot:nth-child(1) {
  background: var(--theme-secondary);
}
.loading-dots-step .dot:nth-child(1)::before {
  animation-delay: 0.2s;
}
.loading-dots-step .dot:nth-child(2) {
  background: var(--theme-secondary);
}
.loading-dots-step .dot:nth-child(2)::before {
  animation-delay: 0.4s;
}
.loading-dots-step .dot:nth-child(3) {
  background: var(--theme-secondary);
}
.loading-dots-step .dot:nth-child(3)::before {
  animation-delay: 0.6s;
}
.loading-dots-step .dot:nth-child(4) {
  background: var(--theme-secondary);
}
.loading-dots-step-step .dot:nth-child(4)::before {
  animation-delay: 0.8s;
}
.loading-dots-step .dot:nth-child(5) {
  background: var(--theme-secondary);
}
.loading-dots-step .dot:nth-child(5)::before {
  animation-delay: 1s;
}
@keyframes wave {
  50%,
  75% {
    transform: scale(2.5);
  }
  80%,
  100% {
    opacity: 0;
  }
}

.loading-step .logo-loading {
  width: 200px;
  margin-bottom: 30px;
}
